* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
h1 {
  color: rgb(54, 54, 54);
  font-size: medium;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 2rem;
}
h2 h3 {
  color: rgb(54, 54, 54);
}
body {
  font-family: "Lato", sans-serif;
}
.App {
  transition: all 0.5s ease;
}
.library-active {
  margin-left: 30%;
}

h3,
h4 {
  font-weight: 400;
  color: rgb(10, 10, 10);
}

@import "./song";
@import "./player";
@import "./library";
@import "./nav";
